import React, { useState, useEffect } from 'react';

import bridge from '@vkontakte/vk-bridge';

import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home'
import Organizations from './panels/Organizations'
import Covid from './panels/Covid'
import Weather from './panels/Weather'
import News from './panels/News'
import Education from './panels/Education'
import Taxi from './panels/Taxi'
import Example from './panels/Example'
import RoomOne from './panels/RoomOne'
import Info from './panels/Info'
import RusLang from './class/classnine/Rus'
import Still from './panels/Still'
import School from './panels/School'
import SchoolTwo from './panels/SchoolTwo'
import SchoolThree from './panels/SchoolThree'
import CodeTest from './dnevnik/CodeTest'
import Lock from './panels/Lock'
import CovidWorld from './panels/CovidWorld'
import Mask from './panels/Mask'
import Update from './panels/Update'

bridge.send('VKWebAppInit');
const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel}>
           <Home id="home" go={go} />
           <Organizations id="organizations" go={go} />
           <Covid id="covid" go={go} />
           <Weather id="weather" go={go} />
           <News id="news" go={go} />
           <Education id="education" go={go} />
           <Taxi id="taxi" go={go} />
           <Example id="example" go={go} />
           <RoomOne id="ro" go={go} />
           <Still id="still" go={go} />
           <RusLang id="rl" go={go} />
           <School id="school" go={go} />
           <SchoolTwo id="schooltwo" go={go} />
           <SchoolThree id="schoolthree" go={go} />
           <CodeTest id="codetest" go={go} />
           <Info id="info" go={go} />
           <Lock id="lock" go={go} />
           <Weather id="weath" go={go} />
           <CovidWorld id="covidworld" go={go} />
           <Mask id="mask" go={go} />
           <Update id='update' fetchedUser={fetchedUser} go={go} />
		</View>
	);
}

export default App;




